.dots {
	li:not(:last-child):after {
		content: "•";
		padding-left: 9px;
	}
}
// Used in item-headers and the profile
.list {
	display: flex;
	flex-wrap: wrap;
	li {
		margin-right: 10px;
		&.icon {
			margin-right: 8px;
		}
	}
}

ul,
ol {
	margin: 0;
	padding: 0 0 0 17px;
	ul,
	ol {
		padding: 0 0 0 17px;
	}
}

dl {
	margin: 0;
}

dt {
	font-weight: 600;
	margin: 0;
}

dd {
	margin: 0;
}

